/**
 * @name uiBbIndeterminateDirective
 * @type {function}
 */
const uiBbIndeterminateDirective = () => ({
  restrict: 'A',
  compile: (tElm, tAttrs) => {
    if (!tAttrs.type || tAttrs.type.toLowerCase() !== 'checkbox') {
      return false;
    }
    return ($scope, elm, attrs) => {
      $scope.$watch(attrs.indeterminateNg, newVal => {
        Object.assign(elm[0], { indeterminate: !!newVal });
      });
    };
  },
});

export default uiBbIndeterminateDirective;



